
.login .login-page {
  margin: 0 auto;
  width: 370px;
  height: 230px;
  border-radius: 4px;
  border: 1px solid green;
  box-shadow: #666 0px 0px 20px;
  box-shadow: 2px,2px;
}
.login .login-page .a_registe {
    font-size: 12px;
    margin-left: 80%;
}
.login .login-page .form-group {
    padding-top: 7px;
    text-align: center;
}
.login .login-page .form-group .username, .login .login-page .form-group .password {
      margin: 7 auto;
      width: 90%;
      border: 0;
      background-color: transparent;
      border-bottom: 1px solid #b1b1b1;
      text-align: left;
      box-shadow: transparent 0px 0px 0px;
      border-radius: 0px;
}
.login .login-page .form-group .username:focus {
      border-bottom: 1px solid green;
}
.login .login-page .form-group .password:focus {
      border-bottom: 1px solid green;
}
.login .login-page .form-group button {
      width: 70px;
}
.login .login-page .form-group .btn-cancel {
      float: left;
      margin-top: 10px;
      margin-left: 100px;
      box-shadow: orange 0px 0px 7px;
}
.login .login-page .form-group .btn-login {
      float: right;
      margin-top: 10px;
      margin-right: 100px;
      box-shadow: #49afcd 0px 0px 7px;
}
.login .login-page .form-group .btn-login-disable {
      float: right;
      margin-top: 10px;
      margin-right: 100px;
      box-shadow: gray 0px 0px 7px;
}
.login .registe-page {
  margin: 0 auto;
  width: 370px;
  height: auto;
  border-radius: 4px;
  border: 1px solid green;
  box-shadow: #666 0px 0px 20px;
  padding-bottom: 10px;
  box-shadow: 2px,2px;
}
.login .registe-page .a_login {
    margin-left: 80%;
    font-size: 12px;
    margin-bottom: 30px;
}
.login .registe-page .form-group {
    padding-top: 7px;
    text-align: center;
}
.login .registe-page .form-group .username, .login .registe-page .form-group .password, .login .registe-page .form-group .re_password, .login .registe-page .form-group .real_name, .login .registe-page .form-group .re_password_disable {
      margin: 7 auto;
      width: 90%;
      border: 0;
      background-color: transparent;
      border-bottom: 1px solid #b1b1b1;
      text-align: left;
      box-shadow: transparent 0px 0px 0px;
      border-radius: 0px;
}
.login .registe-page .form-group .username:focus {
      border-bottom: 1px solid green;
}
.login .registe-page .form-group .password:focus {
      border-bottom: 1px solid green;
}
.login .registe-page .form-group .re_password:focus {
      border-bottom: 1px solid green;
}
.login .registe-page .form-group .real_name:focus {
      border-bottom: 1px solid green;
}
.login .registe-page .form-group .re_password_disable {
      border-bottom: 1px solid red;
}
.login .registe-page .form-group button {
      width: 70px;
}
.login .registe-page .form-group .btn-cancel {
      float: left;
      margin-top: 10px;
      margin-left: 100px;
      box-shadow: orange 0px 0px 7px;
}
.login .registe-page .form-group .btn-registe {
      margin-top: 10px;
      box-shadow: #49afcd 0px 0px 7px;
}
.login .registe-page .form-group .btn-registe-disable {
      margin-top: 10px;
      box-shadow: gray 0px 0px 7px;
}
.login .haslogin {
  padding-top: 30px;
}
.login .haslogin h2 {
    font-size: 22px;
}
.login .haslogin img {
    width: 170px;
}
.login .haslogin a {
    font-size: 12px;
    display: block;
    margin-top: 20px;
}



