
@charset "UTF-8";
*， *:after, *:before {
  box-sizing: border-box;
}
img {
  max-width: 100%;
}
.time-line {
  position: static;
  overflow: hidden;
  display: block;
  float: inherit;
  height: 100%;
  width: 98%;
  padding: 1% 1% 0;
}
.time-line .show-area {
    background-color: #f3fef2;
    width: 75%;
    height: 100%;
    min-height: 400px;
    float: left;
}
.time-line .show-area .cd-container {
      width: 90%;
      max-width: 1170px;
      margin: 0 auto;
}
.time-line .show-area .cd-container::after {
      content: '';
      display: table;
      clear: both;
}
.time-line .show-area #cd-timeline {
      position: relative;
      padding: 2em 0;
      margin-top: 2em;
      margin-bottom: 2rem;
}
.time-line .show-area #cd-timeline::before {
      content: '';
      position: absolute;
      top: 0;
      left: 18px;
      height: 100%;
      width: 4px;
      background: #d7e4ed;
}
@media only screen and (min-width: 800px) {
.time-line .show-area #cd-timeline {
        margin-top: 3em;
        margin-bottom: 3em;
}
.time-line .show-area #cd-timeline::before {
        left: 50%;
        margin-left: -2px;
}
}
.time-line .show-area .cd-timeline-block {
      position: relative;
      margin: 2em 0;
}
.time-line .show-area .cd-timeline-block::after {
      content: '';
      display: table;
      clear: both;
}
.time-line .show-area .cd-timeline-block:first-child {
      margin-top: 0;
}
.time-line .show-area .cd-timeline-block:last-child {
      margin-bottom: 0;
}
@media only screen and (min-width: 1170px) {
.time-line .show-area .cd-timeline-block {
        margin: 4em 0;
}
.time-line .show-area .cd-timeline-block:first-child {
        margin-top: 0;
}
.time-line .show-area .cd-timeline-block:last-child {
        margin-bottom: 0;
}
}
.time-line .show-area .cd-timeline-img {
      position: absolute;
      top: 0;
      left: 0;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      box-shadow: 0 0 0 4px white, inset 0 2px 0 rgba(0, 0, 0, 0.08), 0 3px 0 4px rgba(0, 0, 0, 0.05);
}
.time-line .show-area .cd-timeline-img img {
      display: block;
      width: 24px;
      height: 24px;
      position: relative;
      left: 50%;
      top: 50%;
      margin-left: -12px;
      margin-top: -12px;
}
.time-line .show-area .cd-timeline-img.cd-picture {
      background: #75ce66;
}
.time-line .show-area .cd-timeline-img.cd-movie {
      background: #c03b44;
}
.time-line .show-area .cd-timeline-img.cd-location {
      background: #f0ca45;
}
@media only screen and (min-width: 1170px) {
.time-line .show-area .cd-timeline-img {
        width: 60px;
        height: 60px;
        left: 50%;
        margin-left: -30px;
        /* Force Hardware Acceleration in WebKit */
        -webkit-transform: translateZ(0);
        -webkit-backface-visibility: hidden;
}
.time-line .show-area .cssanimations .cd-timeline-img.is-hidden {
        visibility: hidden;
}
.time-line .show-area .cssanimations .cd-timeline-img.bounce-in {
        visibility: visible;
        -webkit-animation: cd-bounce-1 0.6s;
        animation: cd-bounce-1 0.6s;
}
}
.time-line .show-area .cd-timeline-content {
      position: relative;
      margin-left: 60px;
      background: white;
      border-radius: 0.25em;
      padding: 1em;
      box-shadow: 0 3px 0 #d7e4ed;
}
.time-line .show-area .cd-timeline-content:after {
      content: "";
      display: table;
      clear: both;
}
.time-line .show-area .cd-timeline-content h2 {
      color: #303e49;
}
.time-line .show-area .cd-timeline-content p, .time-line .show-area .cd-timeline-content .cd-read-more, .time-line .show-area .cd-timeline-content .cd-date {
      font-size: 13px;
      font-size: 0.8125rem;
}
.time-line .show-area .cd-timeline-content .cd-read-more, .time-line .show-area .cd-timeline-content .cd-date {
      display: inline-block;
}
.time-line .show-area .cd-timeline-content p {
      margin: 1em 0;
      line-height: 1.6;
}
.time-line .show-area .cd-timeline-content .cd-read-more {
      float: right;
      padding: .8em 1em;
      background: #acb7c0;
      color: white;
      border-radius: 0.25em;
}
.time-line .show-area .no-touch .cd-timeline-content .cd-read-more:hover {
      background-color: #bac4cb;
}
.time-line .show-area a.cd-read-more:hover {
      text-decoration: none;
      background-color: #424242;
}
.time-line .show-area .cd-timeline-content .cd-date {
      float: left;
      padding: .8em 0;
      opacity: .7;
}
.time-line .show-area .cd-timeline-content::before {
      content: '';
      position: absolute;
      top: 16px;
      right: 100%;
      height: 0;
      width: 0;
      border: 7px solid transparent;
      border-right: 7px solid white;
}
@media only screen and (min-width: 768px) {
.time-line .show-area .cd-timeline-content h2 {
        font-size: 20px;
        font-size: 1.25rem;
}
.time-line .show-area .cd-timeline-content p {
        font-size: 16px;
        font-size: 1rem;
}
.time-line .show-area .cd-timeline-content .cd-read-more, .time-line .show-area .cd-timeline-content .cd-date {
        font-size: 14px;
        font-size: 0.875rem;
}
}
@media only screen and (min-width: 1170px) {
.time-line .show-area .cd-timeline-content {
        margin-left: 0em;
        padding: 1.6em;
        width: 42%;
}
.time-line .show-area .cd-timeline-content::before {
        top: 24px;
        left: 100%;
        border-color: transparent;
        border-left-color: white;
}
.time-line .show-area .cd-timeline-content .cd-read-more {
        float: left;
}
.time-line .show-area .cd-timeline-content .cd-date {
        position: absolute;
        width: 100%;
        left: 80%;
        top: 6px;
        font-size: 16px;
        font-size: 1rem;
}
.time-line .show-area .cd-timeline-block:nth-child(even) .cd-timeline-content {
        float: right;
}
.time-line .show-area .cd-timeline-block:nth-child(even) .cd-timeline-content::before {
        top: 24px;
        left: auto;
        right: 100%;
        border-color: transparent;
        border-right-color: white;
}
.time-line .show-area .cd-timeline-block:nth-child(even) .cd-timeline-content .cd-read-more {
        float: right;
}
.time-line .show-area .cd-timeline-block:nth-child(even) .cd-timeline-content .cd-date {
        left: auto;
        right: 122%;
        text-align: right;
}
.time-line .show-area .cssanimations .cd-timeline-content.is-hidden {
        visibility: hidden;
}
.time-line .show-area .cssanimations .cd-timeline-content.bounce-in {
        visibility: visible;
        -webkit-animation: cd-bounce-2 0.6s;
        animation: cd-bounce-2 0.6s;
}
}
@media only screen and (min-width: 1170px) {
.time-line .show-area {
        /* inverse bounce effect on even content blocks */
}
.time-line .show-area .cssanimations .cd-timeline-block:nth-child(even) .cd-timeline-content.bounce-in {
          -webkit-animation: cd-bounce-2-inverse 0.6s;
          animation: cd-bounce-2-inverse 0.6s;
}
}
.time-line .show-area ul {
      list-style-type: none;
}
.time-line .add-line {
    position: relative;
    display: block;
    background-color: #989082;
    width: 25%;
    height: 100%;
    float: left;
    min-height: 400px;
    overflow: hidden;
}
.time-line .add-line form {
      border: none;
      width: 100%;
      padding: 0 auto;
      text-align: center;
}
.time-line .add-line form .form-group {
        margin: 0 auto;
        border: none;
}
.time-line .add-line form .form-group h3 {
          color: green;
}
.time-line .add-line form .form-group input {
          width: 100%;
          border: 0;
          font-size: 20px;
          background-color: transparent;
          border-bottom: 1px solid #b1b1b1;
          text-align: center;
          box-shadow: transparent 0px 0px 0px;
          border-radius: 0px;
}
.time-line .add-line form .form-group .add-area {
          border: 0;
          width: 97%;
          font-size: 20px;
}
.time-line .add-line form .form-group button {
          width: 100px;
          height: 50px;
          font-size: 22px;
          border-radius: 30px;
          border: 0;
}



