
.menu ul {
  width: 55%;
  min-width: 906px;
  list-style-type: none;
  font-size: 30px;
}
.menu ul li {
    border-radius: 5px;
    box-shadow: 1px 1px 2px;
    margin-bottom: 5px;
    height: 40px;
    background-color: rgba(123, 123, 123, 0.5);
    padding-top: 10px;
    padding-left: 6px;
}
.menu ul li .menu-title {
      float: left;
      width: 30%;
      min-width: 270px;
      text-align: left;
      display: block;
      text-overflow: hidden;
}
.menu ul li .menu-arow {
      float: left;
      height: 100%;
      display: block;
      color: green;
      width: 20%;
      min-width: 180px;
      text-overflow: hidden;
}
.menu ul li .menu-url {
      margin-left: 6px;
      height: 100%;
      float: left;
      text-align: left;
      display: block;
      text-overflow: hidden;
      width: 40%;
      min-width: 360px;
      color: blue;
}
.menu ul li .menu-operation {
      display: initial;
      margin-top: 0px;
      text-align: left;
      font-size: 23px;
      width: 10%;
      min-width: 90px;
}
.menu ul li .menu-operation .glyphicon {
        margin-left: 4px;
}



