
@charset "UTF-8";
.lifelist h3 {
  color: green;
  margin-left: 10px;
}
.lifelist .disklist {
  overflow: hidden;
}
.lifelist .disklist .block {
    border-radius: 1vw;
    width: 20vw;
    height: 10vw;
    display: block;
    background-color: rgba(128, 128, 128, 0.5);
    margin-left: 4vw;
    margin-top: 3vw;
    float: left;
    position: relative;
}
.lifelist .disklist .block .blockdown {
      border-radius: 1vw;
      overflow: hidden;
      position: absolute;
      top: 8.5vw;
      left: 0vw;
      width: 100%;
      height: 1.5vw;
      background-color: rgba(0, 0, 0, 0.3);
}
.lifelist .disklist :hover .blockdown {
    animation: myfirst 1s forwards;
    -moz-animation: myfirst 1s forwards;
    /* Firefox */
    -webkit-animation: myfirst 1s forwards;
    /* Safari 和 Chrome */
    -o-animation: myfirst 1s forwards;
    /* Opera */
}
@keyframes myfirst {
from {
    top: 8.5vw;
    height: 1.5vw;
}
to {
    top: 0vw;
    height: 10vw;
}
}
@-webkit-keyframes myfirst /* Safari and Chrome */ {
from {
    top: 8.5vw;
    height: 1.5vw;
}
to {
    top: 0vw;
    height: 10vw;
}
}



